.Icon,
.Logo {
  height: 1em;
  width: auto;
  padding: 0.02em;
  fill: currentcolor; }
  .Icon.fallback,
  .Logo.fallback {
    font-family: "Roboto Condensed", sans-serif;
    vertical-align: text-top;
    display: inline;
    font-size: 0.4em;
    height: 0.5em;
    padding: 0.1em 0.3em;
    margin: 0.1em 0;
    color: black;
    font-weight: 400;
    background-color: rgba(255, 255, 255, 0.6); }



